<template>
  <div>
    <el-row>
      <el-col :span="11">
        <el-tabs v-model="activeLIndex" type="border-card">
          <el-tab-pane :label="item.name" v-for="(item,index) in leftTabs" :key="index">
            <div class="grid-content bg-purple">
              <el-input type="textarea" class="textarea" :rows="25" placeholder="" v-model="item.text">
              </el-input>
            </div>
          </el-tab-pane>
        </el-tabs>
        <!-- <el-radio-group v-model="lRadioIndex" class="">
          <el-radio :label="index" v-for="(item,index) in leftTabs" :key="index">{{item.name}}</el-radio>
        </el-radio-group>
        <div class="grid-content bg-purple">
          <el-input type="textarea" class="textarea" :rows="25" placeholder="" v-model="sourceTextarea">
          </el-input>
        </div> -->
      </el-col>
      <el-col :span="1">
        <div class="dot-tip">To</div>
      </el-col>

      <el-col :span="11">
        <el-tabs v-model="activeRIndex" type="border-card">
          <el-tab-pane :label="item.name" v-for="(item,index) in rightSliceTabs" :key="index" :disabled='item.disabled'>
            <div class="grid-content bg-purple-light">
              <el-input type="textarea" class="textarea" :disabled='isBanInput' :rows="25" placeholder="" v-model="item.text">
              </el-input>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>

</template>

<script>
  import * as htmltoless from 'htmltoless'
  import {
    html2wxml
  } from 'htmltowxml';
  import less from 'less';
  export default {
    name: '',
    data() {
      return {
        activeLIndex: 0,
        activeRIndex: 0,
        leftTabs: [{
          name: 'html',
          text: ``,
          to: `less,wxml`,
        }],
        isBanInput: false,
        sourceText: ``,
        allowTo: [], //
      }
    },
    computed: {
      rightSliceTabs() {
        let to = this.leftTabs[this.activeLIndex].to;
        let tos = to.split(',');
        this.rightTabs.forEach(item => {
          item.disabled = !tos.includes(item.name);
        })
        return this.rightTabs;
      },
      rightTabs() {
        let to = this.leftTabs[this.activeLIndex].to
        let tos = to.split(',');
        let tabs = tos.map(item => {
          return {
            name: item,
          }
        })
        return tabs
      }
    },
    watch: {
      activeLIndex: {
        handler() {
          this.calAllowConverItems();
          this.handleConversion();
        },
        immediate: true,
      },
      activeRIndex: {
        handler() {
          this.handleConversion();
        },
        immediate: true,
      },
      leftTabs: {
        handler(value) {
          if (!value.text) {
            this.rightTabs.forEach(item => {
              item.text = '';
            })
          }
          this.handleConversion();
        },
        // immediate: true,
        deep: true,
      }
    },

    created() {
      console.log(less)
      
    },

    methods: {
      handleConversion() {
        let source = this.leftTabs[this.activeLIndex].name
        let to = this.rightSliceTabs[this.activeRIndex].name
        this.leftText = this.leftTabs[this.activeLIndex].text;
        if (source === 'html' && to === 'less') {
          this.htmlToLess();
        } else if (source === 'html' && to === 'wxml') {
          this.htmlToWxml();
        }
      },
      //htmltoless
      htmlToLess() {
        if (!this.sourceText) return
        const less = htmltoless.parse(this.sourceText);
        // this.isBanInput = true;
        this.appendRightText(less)
      },
      htmlToWxml() {
        if (!this.sourceText) return
        let wxml = html2wxml(this.sourceText);
        this.appendRightText(wxml);
      },
      //htmltotext
      htmlToText() {
        const text = convert(html, {
          wordwrap: 130
        });
        this.appendRightText(text);
      },
      appendRightText(text) {
        this.rightSliceTabs[this.activeRIndex].text = text || ''
      },
      //
      calAllowConverItems() {
        let to = this.leftTabs[this.activeRIndex].to;
        this.allowTo = to.split(',');

      }

    }

  }
</script>

<style lang="less" scoped>
  .grid-content {
    margin-top: 20px;
  }

  .dot-tip {
    font-weight: bold;
    text-align: center;
    line-height: 3;
  }

  .textarea {
    min-height: 500px;
  }
</style>


